import { Injectable } from "@angular/core";
import { environment } from "../../environments/environment";
import { HttpClient, HttpHeaders  } from "@angular/common/http";
import { Observable, Subject } from "rxjs";

@Injectable({
  providedIn: "root"
})
export class GlobalService {
  apiKey = 'fdHSuR2s$jYX!Vw@TK^naVjq^Xi6ro0giTRlC7jLZWR^exuMxdWtZ&Z69SoVBJZq';
  baseUrl = environment.BASEURL;
  private subject = new Subject<any>();
  private loadingSubject = new Subject<any>();
  private companySubject = new Subject<any>();
  getActionChildToParent(): Observable<any> {
    return this.subject.asObservable();
  }
  sendActionChildToParent(action: string) {
    this.subject.next({ text: action });
  }

  getLoadingLabel(): Observable<any> {
    return this.loadingSubject.asObservable();
  }
  setLoadingLabel(action: string) {
    this.loadingSubject.next({ text: action });
  }
  
  getCompanyObject(): Observable<any> {
    return this.companySubject.asObservable();
  }
  setCompanyObject(object: any) {
    this.companySubject.next(object);
  }

  constructor(
    private http: HttpClient,
  ) {
    
  }

  public getAccess(param: object): Observable<any> {
    let headers = new HttpHeaders({'api-key': this.apiKey});
    let url = `${this.baseUrl}validateUser?email=${param['email']}&createdt=${param['createdt']}&source=${param['source']}`;
    return this.http.get(url, { headers });
  }
  
  public getConfig(param: object): Observable<any> {
    let headers = new HttpHeaders({'api-key': this.apiKey});
    let url = `${this.baseUrl}config`;
    return this.http.get(url, { headers });
  }

  public getCompanies(param: object): Observable<any> {
    let headers = new HttpHeaders({'api-key': this.apiKey});
    let url = `${this.baseUrl}companies?user=${param['user']}`;
    if(param['page']){
      url+= `&page=${param['page']}`;
    }
    if(param['limit']){
      url+= `&limit=${param['limit']}`;
    }
    if(param['isActive'] && param['isActive'] != ""){
      url+= `&isActive=${param['isActive']}`;
    }
    if(param['sort']){
      url+= `&sort=${param['sort']}`;
    }
    if(param['sortOrder']){
      url+= `&sortOrder=${param['sortOrder']}`;
    }
    return this.http.get(url, { headers })
  }
  
  public getUsers(param: object): Observable<any> {
    let headers = new HttpHeaders({'api-key': this.apiKey});
    let url = `${this.baseUrl}users?company=${param['company']}&user=${param['user']}`;
    if(param['page']){
      url+= `&page=${param['page']}`;
    }
    if(param['group']){
      url+= `&group=${param['group']}`;
    }
    if(param['limit']){
      url+= `&limit=${param['limit']}`;
    }
    if(param['isActive'] == 1){
      url+= `&isActive=1`;
    }
    if(param['isActive'] == 0){
      url+= `&isActive=0`;
    }
    if(param['sortOrder']){
      url+= `&sortOrder=${param['sortOrder']}`;
    }
    if(param['sort']){
      url+= `&sort=${param['sort']}`;
    }
    if(param['search']){
      url+= `&search=${param['search']}`;
    }
    return this.http.get(url, { headers })
  }

  public getStatus(param: object): Observable<any> {
    let headers = new HttpHeaders({'api-key': this.apiKey});
    let url = `${this.baseUrl}bulkImportStatus?company=${param['company']}&user=${param['user']}`;
    if(param['source']){
      url+= `&source=${param['source']}`;
    }
    if(param['status']){
      url+= `&status=${param['status']}`;
    }
    if(param['page']){
      url+= `&page=${param['page']}`;
    }
    if(param['limit']){
      url+= `&limit=${param['limit']}`;
    }
    if(param['sort']){
      url+= `&sort=${param['sort']}`;
    }
    if(param['sortOrder']){
      url+= `&sortOrder=${param['sortOrder']}`;
    }
    return this.http.get(url, { headers })
  }
  
  public getUserById(param: object): Observable<any> {
    let headers = new HttpHeaders({'api-key': this.apiKey});
    let url = `${this.baseUrl}users/${param['id']}?company=${param['company']}&user=${param['user']}`;
    return this.http.get(url, { headers });
  }
  
  public getCompanyById(param: object): Observable<any> {
    let headers = new HttpHeaders({'api-key': this.apiKey});
    let url = `${this.baseUrl}companies/${param['id']}?user=${param['user']}`;
    return this.http.get(url, { headers })
  }
  
  public saveCompany(param: object): Observable<any> {
    let headers = new HttpHeaders({ 'api-key': this.apiKey });
    let url = `${this.baseUrl}companies`;
    return this.http.post(url, param, { headers });
  }
  
  public updateCompany(param: object, id: any): Observable<any> {
    let headers = new HttpHeaders({'api-key': this.apiKey});
    let url = `${this.baseUrl}companies/${id}`;
    return this.http.put(url, param, { headers: headers });
  }
  
  public saveUser(param: object): Observable<any> {
    let headers = new HttpHeaders({ 'api-key': this.apiKey });
    let url = `${this.baseUrl}users`;
    return this.http.post(url, param, { headers });
  }
  public updateUser(param: object, id: any): Observable<any> {
    let headers = new HttpHeaders({'api-key': this.apiKey});
    let url = `${this.baseUrl}users/${id}`;
    return this.http.put(url, param, { headers: headers });
  }

  
  public emailAlreadyExits(param: object): any {
    let apiURL = `${this.baseUrl}users/emailAlreadyExits`;
    return this.http.post(apiURL, param);
  }

  public ValidateEmail(inputText: string) {
    var mailformat = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/;
    if (inputText.match(mailformat)) {
      return true;
    } else {
      return false;
    }
  }
  
  public topscroll() {
    let top = document.getElementById('container');
    let top2 = document.getElementById('admin-container');
    if (top !== null) {
      top.scrollIntoView();
      top = null;
    }else if (top2 !== null) {
      top2.scrollIntoView();
      top2 = null;
    }
  }
  
  uploadFile(file: any, param: any) {
    const newName = file.name.replace(/ /g, '-');
    const formData = new FormData();
    formData.append('csv', file, newName);
    let headers = new HttpHeaders({ 'api-key': this.apiKey, 'Content-Type': 'text/csv' });
    let url = `${this.baseUrl}bulkImport?company=${param['company']}&user=${param['user']}`;
    return this.http.post(url, file,  { headers: headers });
  }
  
  public exportData(param: any){
    let headers = new HttpHeaders({ 'api-key': this.apiKey, 'Content-Type': 'text/csv', Accept: 'application/csv' });
    let url = `${this.baseUrl}bulkExport?company=${param['company']}&user=${param['user']}`;
    if(param['isActive'] && param['isActive'] != ""){
      url+= `&isActive=${param['isActive']}`;
    }
    if(param['group']){
      url+= `&group=${param['group']}`;
    }
    if(param['search']){
      url+= `&search=${param['search']}`;
    }
    return this.http.get(url, { headers: headers });
    /* let httpOptions = {
      headers: headers,
      responseType: 'text/csv'
      };
    return this.http.get(url, { httpOptions }); */

  }

}
