import { Component, OnInit, ViewChild } from '@angular/core';
import { ViewEncapsulation } from '@angular/core';
import { currentUser } from './user.model';
import { JwtService } from '../../shared-ui/jwt.service';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { Subscription } from 'rxjs';
import { ModalDirective } from 'ngx-bootstrap/modal';
import { GlobalService } from '../../shared-ui/global.service';
import { environment } from '../../../environments/environment';


@Component({
  selector: 'app-ui',
  templateUrl: './ui.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: [],
})

export class UiComponent implements OnInit {
  subscription: Subscription;
  fullYear = new Date().getFullYear();
  currentUser: currentUser = new currentUser;
  quickSearchList: any[] = [];
  quickSearchText: any = {
    searchText: ""
  };
  loadingListings: boolean = false;
  @ViewChild('quickSearchOverlayWrap', {static: false}) public quickSearchOverlayWrap: ModalDirective;
  constructor(
    private jwtService: JwtService,
    private router: Router,
    private globalService: GlobalService,
    private toastr: ToastrService
  ) { 
    this.currentUser = JSON.parse(this.jwtService.getCurrentUser());
    this.subscription = this.globalService.getActionChildToParent().subscribe(message => {
      if (message) {
        this.currentUser = JSON.parse(this.jwtService.getCurrentUser());
      }
    });
  }
  
  ngOnInit() {
    this.currentUser = JSON.parse(this.jwtService.getCurrentUser());
    
  }

  logout(){
    this.jwtService.destroyToken();
    this.router.navigate(['/']);
    this.toastr.success('You have logged out successfully. ','Success');
    this.globalService.sendActionChildToParent('loggedOut');
    this.currentUser = JSON.parse(this.jwtService.getCurrentUser());
  }

}
