import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AuthLayoutComponent } from './auth-layout.component';


const routes: Routes = [
  {
    path: '',
    component: AuthLayoutComponent,
    children: [
      {
        path: 'validateUser/:email/:createdt',
        loadChildren: () => import('./../../views/auth-pages/iframe-access/iframe-access.module').then(mod => mod.IframeAccessModule)
      },
      {
        path: '404',
        loadChildren: () => import('./../../views/auth-pages/error404/error404.module').then(mod => mod.Error404Module)
      },
      {
        path: 'thank-you',
        loadChildren: () => import('./../../views/auth-pages/thank-you/thank-you.module').then(mod => mod.ThankYouModule)
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AuthLayoutRoutingModule { }
