import { Component, OnDestroy, Inject } from '@angular/core';
import { navItems } from '../../_nav';
import { Router } from '@angular/router';
import { JwtService } from '../../shared-ui/jwt.service';
import { currentUser } from '../home-layout/user.model';
import { Subscription } from 'rxjs';
import { GlobalService } from '../../shared-ui/global.service';

@Component({
  selector: 'app-dashboard',
  templateUrl: './default-layout.component.html'
})
export class DefaultLayoutComponent implements OnDestroy {
  subscription: Subscription;
  currentUser: currentUser = new currentUser;
  fullYear = new Date().getFullYear();
  public navItems = navItems;
  public sidebarMinimized = true;
  private changes: MutationObserver;
  public element: HTMLElement;
  currentPage: any;

  constructor( private router: Router,
    private jwtService: JwtService,
    private globalService: GlobalService
  ) {
    this.subscription = this.globalService.getActionChildToParent().subscribe(item => {
      if (item) {
        this.currentPage = item.text;
      }
    });
  }
  ngOnInit(){
    this.currentUser = JSON.parse(this.jwtService.getCurrentUser());
    // this.router.navigate(['/dashboard']);
  }

  ngOnDestroy(): void {
    // this.changes.disconnect();
  }

  logout(){
    this.jwtService.destroyToken();
    this.router.navigate(['/thank-you']);
  }
 
}
