import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";
import { DefaultLayoutComponent } from "./default-layout.component";
import { AuthGuard } from "../../shared-ui/auth.guard";

const routes: Routes = [
  {
    path: "",
    component: DefaultLayoutComponent,
    children: [
      {
        path: "dashboard",
        loadChildren: () => import('./../../views/admin-pages/dashboard/dashboard.module').then(mod => mod.DashboardModule),
        canActivate: [ AuthGuard ]
      },
      {
        path: "company/list",
        loadChildren: () => import('./../../views/admin-pages/company/list/list.module').then(mod => mod.ListModule),
        canActivate: [ AuthGuard ]
      },
      {
        path: "company/add",
        loadChildren: () => import('./../../views/admin-pages/company/add/add.module').then(mod => mod.AddModule),
        canActivate: [ AuthGuard ]
      },
      {
        path: "company/edit/:id",
        loadChildren: () => import('./../../views/admin-pages/company/add/add.module').then(mod => mod.AddModule),
        canActivate: [ AuthGuard ]
      },
      {
        path: "user/list",
        loadChildren: () => import('./../../views/admin-pages/user/userlist/userlist.module').then(mod => mod.UserlistModule),
        canActivate: [ AuthGuard ]
      },
      {
        path: "user/list/:companyId",
        loadChildren: () => import('./../../views/admin-pages/user/userlist/userlist.module').then(mod => mod.UserlistModule),
        canActivate: [ AuthGuard ]
      },
      {
        path: "user/add",
        loadChildren: () => import('./../../views/admin-pages/user/useradd/useradd.module').then(mod => mod.UseraddModule),
        canActivate: [ AuthGuard ]
      },
      {
        path: "user/edit/:id",
        loadChildren: () => import('./../../views/admin-pages/user/useradd/useradd.module').then(mod => mod.UseraddModule),
        canActivate: [ AuthGuard ]
      },
      {
        path: "bulk-import-status",
        loadChildren: () => import('./../../views/admin-pages/bulk-import-status/bulk-import-status.module').then(mod => mod.BulkImportStatusModule),
        canActivate: [ AuthGuard ]
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class DefaultRoutingModule {}
