import { ModuleWithProviders, NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { RouterModule } from '@angular/router';
import { SharedUiRoutingModule } from './shared-ui-routing.module';
import { LoadingComponent } from './loading/loading.component';
import { AlertComponent } from './alert';
import { NgxSpinnerModule } from "ngx-spinner";
import { ToastrModule } from "ngx-toastr";
import { ModalModule, BsDropdownModule} from "ngx-bootstrap";
import { CharacterOnlyDirective } from './onlycharacter.directive';
import { NumberOnlyDirective } from './onlynumber.directive';
import { GrdFilterPipe } from './grd-filter.pipe';
import { OwlModule } from 'ngx-owl-carousel';
import { PDFExportModule } from '@progress/kendo-angular-pdf-export';
import { AgmCoreModule } from '@agm/core';
import { NgxPaginationModule } from 'ngx-pagination';
import { OrderModule } from 'ngx-order-pipe';


const SHARED_COMPONENTS = [
  LoadingComponent, 
  AlertComponent,
  CharacterOnlyDirective,
  NumberOnlyDirective,
  GrdFilterPipe,
];

@NgModule({
  imports: [
    CommonModule,
    ReactiveFormsModule,
    RouterModule,
    SharedUiRoutingModule,
    FormsModule,
    ToastrModule.forRoot({
      closeButton: true,
      positionClass: "toast-bottom-right",
      preventDuplicates: true,
    }),
    NgxPaginationModule,
    OrderModule,
    OwlModule,
    NgxSpinnerModule,
    ModalModule.forRoot(),
    BsDropdownModule.forRoot(),
    PDFExportModule,
    AgmCoreModule.forRoot({
      apiKey: "",
      libraries: ["places"]
    }),
  ],
  providers: [],
  declarations: SHARED_COMPONENTS,
  exports: SHARED_COMPONENTS,
  entryComponents: []
})
export class SharedUiModule {
  static forRoot(): ModuleWithProviders {
    return {
      ngModule: SharedUiModule,
      providers: []
    };
  }
}
